.App
{
  padding: 60px 0;
}
@-webkit-keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes fadeIn
{
  0%
  {
    opacity: 0;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
  100%
  {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@media screen and (max-width: 767px)
{
  .App
  {
    padding: 20px 0;
  }
}



