.navigation
{
  text-align: center;
  margin-bottom: 50px;
}
.navigation__item
{
  display: inline-block;
  -webkit-transition: ease .3s;
  -o-transition: ease .3s;
  transition: ease .3s;
  opacity: 0.5;
}
.navigation__item:not(:last-of-type)
{
  margin-right: 30px;
}
.navigation__item.active
{
  opacity: 1;
}
@media screen and (max-width: 767px)
{
  .navigation
  {
    margin-bottom: 40px;
  }
}



