.about
{
  opacity: 0;
  -webkit-animation: fadeIn ease .6s forwards;
          animation: fadeIn ease .6s forwards;
  padding: 0 10%;
}
.about__image
{
  display: block;
  margin: 0 auto;
  border-radius: 50%;
  margin-bottom: 25px;
}
.about__title
{
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 25px;
}
.about__description
{
  font-size: 16px;
  line-height: 1.4;
  max-width: 800px;
  margin: 0 auto;
  margin-bottom: 15px;
}
.underline
{
  text-decoration: underline;
}
.about strong
{
  display: block;
  text-align: center;
  margin-bottom: 10px;
}
@media screen and (max-width: 991px)
{
  .about
  {
    padding: 0 40px;
  }
}
@media screen and (max-width: 767px)
{
  .about
  {
    padding: 0 20px;
  }
}



