.contact
{
  padding: 0 20px;
  -webkit-animation: fadeIn ease .6s forwards;
          animation: fadeIn ease .6s forwards;
}
.contact__title
{
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 25px;
}
.contact__image
{
  max-width: 350px;
  width: 100%;
  display: block;
  margin: 0 auto;
  -webkit-animation: float ease-in-out 4s infinite alternate;
          animation: float ease-in-out 4s infinite alternate;
  margin-bottom: 20px;
}
@-webkit-keyframes float
{
  0%
  {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  100%
  {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
}
@keyframes float
{
  0%
  {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  100%
  {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
}
.contact__links-title
{
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
}
.contact__links
{
  text-align: center;
}
.contact-link
{
  margin-bottom: 15px;
}
.contact-link::before
{
  display: inline-block;
  vertical-align: middle;
  content: '';
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  margin-right: 10px;
}
.contact-link--linkedin::before
{
  background-image: url('../images/linkedin.svg');
}
.contact-link--github::before
{
  background-image: url('../images/github.svg');
}
.contact-link--gmail::before
{
  background-image: url('../images/gmail.svg');
}



